@import 'ScssHelpers/helpers-all.scss';

.text_block {
	@include bp($point_2, min) {
		padding-top: 11.5rem;
		padding-bottom: 10.4rem;
	}

	@include bp($point_3) {
		padding-top: 5.6rem;
		padding-bottom: 1.6rem;
	}

}

.text_block__content {
	max-width: 79rem;
	margin: 0 auto;
	text-align: center;

	h3 {
		@include h3;
		font-family: $font_2;
		color: $black_2;

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	p {
		@include text;

		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}
}
