@import 'ScssHelpers/helpers-all.scss';

.promo_banner {
	background-color: $white_2;

	@include bp($point_2, min) {
		padding: 9.8rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 5.6rem var(--gl-indent);
	}
}

.promo_banner_in {
	max-width: 133rem;
}

.promo_banner__body {
	display: flex;

	min-height: 59.4rem;

	@include bp($point_3) {
		flex-direction: column;
	}
}

.promo_banner__info {
	display: flex;
	align-items: center;

	color: $white_2;
	background-color: $black_2;

	.promo_banner--reversed_mod & {
		order: 2;

		@include bp($point_2, min) {
			padding: 5rem 13.5rem 5rem 35rem;
		}
	}

	@include bp($point_2, min) {
		flex: 0 1 92.5rem;

		padding: 5rem 35rem 5rem 13.5rem;

		border-radius: $gl-radius;
	}

	@include bp($point_3) {
		justify-content: center;

		padding: 2.7rem 3.2rem 6rem 3.2rem;

		border-radius: 0 0 $gl-radius $gl-radius;

		order: 2;
	}
	h4 {
		text-transform: uppercase;
		font-size: 1.6rem;
		line-height: 2.6;
		letter-spacing: 0.2em;
		font-weight: 700;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.5rem;
			}

			@include bp($point_3) {
				margin-bottom: 0.8rem;
			}
		}
	}

	h3 {
		font-family: $font_2;
		font-size: 3.2rem;
		font-weight: 700;
		line-height: 1.1;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.5rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.2rem;
			}
		}
	}

	p {
		font-weight: 400;
		line-height: 1.3;
		letter-spacing: 0.03em;

		@include bp($point_2, min) {
			font-size: 1.8rem;
		}

		@include bp($point_3) {
			font-size: 1.6rem;
		}

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.2rem;
			}
		}
	}

	a {
		color: inherit;

		@include btn_size_normal;
		@include btn_style_coral;

		&:hover,
		&:focus {
			text-decoration: none;
		}

		@include bp($point_2, min) {
			width: 17.4rem;
		}

		@include bp($point_3) {
			width: 14.5rem;
		}
	}
}

.promo_banner__content {
	width: 100%;
	max-width: 40rem;

	@include bp($point_3) {
		text-align: center;
	}
}

.promo_banner__slider {
	align-self: center;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		transform: scale(0.99);

		background-color: #fff;

		border-radius: $gl_radius;
	}

	.promo_banner--reversed_mod & {
		order: 1;

		@include bp($point_2, min) {
			margin-right: -25rem;
		}
	}

	&:not(.promo_banner--reversed_mod &) {
		@include bp($point_2, min) {
			margin-left: -25rem;
		}
	}
	@include bp($point_2, min) {
		flex: 1 1 65.5rem;

		border-radius: $gl-radius;
	}

	@include bp($point_3) {
		width: 100%;

		border-radius: $gl-radius $gl-radius 0 0;
	}
}

.promo_banner__slider_item {
}

.promo_banner__picture {
	position: relative;

	display: block;

	@include bp($point_2, min) {
		padding-bottom: 79.4%;
	}

	@include bp($point_3) {
		order: 1;

		padding-bottom: 93.8%;

		background: $black_2;
	}
}

.promo_banner__img {
	position: absolute;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
	height: 100%;

	object-fit: cover;

	@include bp($point_2, min) {
		border-radius: $gl-radius;

		filter: drop-shadow(1rem 1rem 4rem rgba(0, 0, 0, 0.2));
	}

	@include bp($point_3) {
		border-radius: $gl-radius $gl-radius 0 0;
	}
}

.promo_banner .swiper-pagination {
	margin-bottom: 2.4rem;

	@include bp($point_3) {
		display: none;
	}
}

.promo_banner .swiper-pagination-bullet {
	width: 1.5rem;
	height: 1.5rem;

	margin: 0 1rem !important;

	background: transparent;

	border: 1px solid $white_2;
	opacity: 1;

	transition: background 0.3s ease, opacity 0.3s ease;
}

.promo_banner .swiper-pagination-bullet-active {
	background: $white_2;
}
