@import 'ScssHelpers/helpers-all.scss';

.full_screen {
	display: flex;
	align-items: center;
	justify-content: center;

	min-height: 100vh;

	padding: 0;
}

.full_screen__bg {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		top: 0;

		display: block;
		width: 100%;
		height: 100%;

		background-color: rgb(0, 0, 0, 0.5);
	}
}

.full_screen__body {
	position: relative;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;

	padding: var(--gl-indent);

	text-align: center;
	color: $white_2;

	z-index: 1;
}

.full_screen__title {
	width: 100%;

	font-family: $font_2;
	font-weight: 700;
	line-height: 1.2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_2, min) {
		max-width: 68rem;

		font-size: 8.4rem;
	}

	@include bp($point_3) {
		max-width: 85%;

		font-size: 3.2rem;
	}
}

.full_screen__subtitle {
	@include subtitle;

	color: $white_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.full_screen__text {
	width: 100%;

	@include text;

	@include bp($point_2, min) {
		max-width: 68rem;

		letter-spacing: 0.03em;
		line-height: 1.3;
	}

	@include bp($point_3) {
		max-width: 30rem;
		letter-spacing: 0.02em;
		line-height: 1.4;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}
}

.full_screen__button {
	cursor: pointer;
}

.full_screen__button_text {
	@include btn_size_normal;
	@include btn_style_coral;

	@include bp($point_2, min) {
		width: 19.2rem;
	}

	@include bp($point_3) {
		width: 18rem;
	}
}

.full_screen__video {
	display: block;

	width: 100%;
	height: 100%;

	object-fit: cover;
}

.full_screen__icon {
	width: 100%;

	@include bp($point_2, min) {
		height: 23.7rem;
	}

	@include bp($point_3) {
		height: 12.5rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.4rem;
		}
	}
}

.full_screen__picture_btn {
	display: block;

	@include bp($point_2, min) {
		width: 9rem;
		height: 9rem;
	}

	@include bp($point_3) {
		width: 5rem;
		height: 5rem;
	}

	transition: transform 0.3s ease;

	&:hover {
		transform: scale(1.05);
	}
}

.full_screen__img_btn {
	display: block;

	width: 100%;
	height: 100%;

	object-fit: contain;
}

.full_screen__picture {
	display: block;
	width: 100%;
	height: 100%;
}

.full_screen__img {
	display: block;

	width: 100%;
	height: 100%;

	object-fit: cover;
}